//  vuex
import Vuex from 'vuex'
Vue.use(Vuex)
//  main.js  car  state  car 
let car = JSON.parse(localStorage.getItem('car') || '[]')
var store = new Vuex.Store({
  state: { // this.$store.state.***
    car: car, // : 
    // { id:id, count: , price: selected: false  }
  },
  mutations: { // this.$store.commit('', '')
    addToCar(state, goodsInfo) {
      //    store  car 
      // 1.
      // 2. car 
      let flag = false // false 
      state.car.some(item => {
        if(item.id == goodsInfo.id){
          item.count += goodsInfo.count
          flag = true
          return true
        }
      })
      if(!flag){
        state.car.push(goodsInfo)
      }
      //  car  localStorage 
      localStorage.setItem('car', JSON.stringify(state.car))
    },
    updateGoodsCount(state, countObj) { 
      //  store 
      state.car.some(item => {
        if(item.id == countObj.id){
          item.count = countObj.count
          return true
        }
      })
      // 
      localStorage.setItem('car', JSON.stringify(state.car))
    },
    removeFromCar(state, id) {
      //  id  car 
      state.car.some((item, i) => {
        if(item.id == id){
          state.car.splice(i, 1)
          return true
        }
      })
      // 
      localStorage.setItem('car', JSON.stringify(state.car))
    },
    updateGoodsSelected(state, slectedObj) {
      //  id  car  selected 
      state.car.some(item => {
        if(item.id == slectedObj.id){
          item.selected = slectedObj.selected
          return true
        }
      })
      // 
      localStorage.setItem('car', JSON.stringify(state.car))
    },
  },
  getters: { // this.$store.getters.***
    // computed filter
    getAllCount(state) {  // 
      let c = 0
      state.car.forEach(item => {
        c += item.count
      })
      return c
    },
    getGoodsCount(state) {  // 
      let o ={}
      state.car.forEach(item => {
        o[item.id] = item.count
      })
      return o
    },
    getGoodsSelected(state) {  //  switch 
      let s = {}
      state.car.forEach(item => {
        s[item.id] = item.selected
      })
      return s
    },
    getSelectedPrice(state) {
      // 
      let a = { allCount: 0, allPrice: 0 }
      state.car.forEach(item => {
        if(item.selected === true) {
          a['allCount'] += item.count
          a['allPrice'] += item.count * item.price
        }
      })
      return a
    },
  },
})

