<template>
  <div class="goodsInfo-container">
    <!--  -->
    <transition @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter" >
      <div class="ball" v-show="ballFlag" ref="ball"></div>
    </transition>
    <!--  -->
    <div class="mui-card">
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <swiper :lunbotuList="lunbotu" :isFull="false"></swiper>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="mui-card">
      <div class="mui-card-header">{{goodsInfo.title}}</div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <p class="price">
            <del>{{goodsInfo.market_price}}</del>&nbsp;&nbsp;&nbsp;<span class="now_price">{{goodsInfo.sell_price}}</span>
          </p>
          <!--  -->
          <p style="color: #000;"><numBtn @getCount="getSelectedCount" :max="goodsInfo.stock_quantity"></numBtn></p>
          <p>
            <mt-button type="primary" size="small"></mt-button>
            <mt-button type="danger" size="small" @click="addToShopCar"></mt-button>
          </p>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="mui-card">
      <div class="mui-card-header"></div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <p>{{goodsInfo.goods_no}}</p>
          <p>{{goodsInfo.stock_quantity}} </p>
          <p>{{goodsInfo.add_time | dateFormat}}</p>
        </div>
      </div>
      <div class="mui-card-footer">
        <mt-button type="primary" size="large" plain @click="goDesc(id)"></mt-button>
        <mt-button type="danger" size="large" plain @click="goComment(id)"></mt-button>
      </div>
    </div>
  </div>
</template>
<script>
// 
import swiper from '../subcomponents/Swiper.vue'
// 
import numBtn from '../subcomponents/GoogsInfo-numBtn.vue'
import { Toast } from 'mint-ui'
export default {
  data() {
    return {
      id: this.$route.params.id, //  id 
      lunbotu: [], // 
      goodsInfo: {}, // 
      ballFlag: false, // 
      selectedCount: 1. // 
    }
  },
  created() {
    this.getLunbotu()
    this.getGoodsInfo()
  },
  methods: {
    getLunbotu() {
      this.$http
      .get('api/getthumimages/' + this.id)
      .then(result => {
        if(result.body.status === 0){
          //  img  item.img 
          result.body.message.forEach(item => {
            item.img = item.src
          })
          this.lunbotu = result.body.message
        }else{
          Toast('')
        }
      })
    },
    getGoodsInfo() { // 
      this.$http
      .get('api/goods/getinfo/' + this.id)
      .then(result => {
        if(result.body.status === 0) {
          this.goodsInfo = result.body.message[0]
        }else{
          Toast('')
        }
      })
    },
    goDesc(id) {
      //  
      this.$router.push({ name:'goodsDesc', params: { id } })
    },
    goComment(id) {
      //  
      this.$router.push({ name:'goodsComment', params: { id } })
    },
    addToShopCar() { // 
      this.ballFlag = !this.ballFlag
      //  { id:id, count: , price: selected: false  }
      //  store  car  
      let goods = { id: parseInt(this.id), count: this.selectedCount, price: this.goodsInfo.sell_price, selected: true }
      //  vuex  mutations  addToCar(state, goodsInfo)  car 
      this.$store.commit("addToCar", goods)
    },
    // 
    beforeEnter(el) {
      el.style.transform = "translate(0, 0)"
    },
    enter(el, done) {
      el.offsetWidth
      // 
      let ballPos = this.$refs.ball.getBoundingClientRect()
      //  DOM 
      let badgePos = document.getElementById('badge').getBoundingClientRect()
      // 
      let xDistance = badgePos.left - ballPos.left
      // 
      let yDistance = badgePos.top - ballPos.top
      el.style.transform = `translate(${xDistance}px, ${yDistance}px)`
      el.style.transition = "all .5s cubic-bezier(.4, -0.3, 1, .68)"
      done()
    },
    afterEnter(el) {
      this.ballFlag = !this.ballFlag
    },
    getSelectedCount(count) {
      //  data 
      this.selectedCount = count
      // console.log(" " + this.selectedCount)
    }
  },
  components: {
    swiper,
    numBtn,
  },
}
</script>
<style lang="scss" scoped>
.goodsInfo-container {
  background-color: #eee;
  overflow: hidden;
  .ball {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: red;
    position: absolute;
    top: 390px;
    left: 147px;
    z-index: 99;
  }
  .price {
    color: #000;
    .now_price {
      color: red;
      font-size: 16px;
      font-weight: 700;
    }
  }
  .mui-card-footer {
    display: block;
    button {
      margin: 15px 0;
    }
  }
  
}
</style>