<template>
  <div class="photolist-container">
    <h3>{{photoInfo.title}}</h3>
    <p class="subtitle">
      <span>{{photoInfo.add_time | dateFormat}}</span>
      <span>{{photoInfo.click}}</span>
    </p>
    <hr>
    <!--  -->
    <mpt-box :id="id"></mpt-box>
    <!--  -->
    <div class="content" v-html="photoInfo.content"></div>
    <!--  -->
    <cmt-box :id="id"></cmt-box>
  </div>
</template>
<script>
  // 
  import Comment from '../subcomponents/Comment.vue'
  // 
  import MiniPhoto from '../subcomponents/MiniPhoto.vue'

  import { Toast } from 'mint-ui'
  export default {
    data() {
      return {
        id: this.$route.params.id, // urlid id 
        photoInfo: {}, // 
        list: [], // 
      }
    },
    created() {
      this.getPhotoInfo()
    },
    methods: {
      getPhotoInfo() {
        this.$http
        .get('api/getimageInfo/' + this.id)
        .then(result => {
          if(result.body.status === 0) {
            this.photoInfo = result.body.message[0]
          }else{
            Toast('')
          }
        })
      },
    },
    components: {
      'cmt-box': Comment,
      'mpt-box': MiniPhoto,
    },
  }
</script>
<style lang="scss" scoped>
.photolist-container {
  padding: 5px;
  h3 {
    font-size: 15px;
    color: #26afff;
    text-align: center;
    margin: 15px 0;
  }
  .subtitle {
    font-size: 13px;
    display: flex;
    justify-content: space-between;
  }
  hr {
    background-color: #ccc;
    margin: 8px 0;
  }
  .content {
    font-size: 13px;
    line-height: 30px;
  }
}
</style>