<template>
  <div class="cmt-container">
    <h3></h3>
    <hr>
    <textarea placeholder="120" maxlength="120" v-model="msg"></textarea>
    <mt-button type="primary" size="large" @click="postComment"></mt-button>
    <!--  -->
    <div class="cmt-list">
      <div class="cmt-item" v-for="(item, i) in comments" :key="item.add_time">
        <div class="cmt-title">
           {{i+1}} &nbsp;&nbsp;{{ item.user_name }}&nbsp;&nbsp;{{ item.add_time | dateFormat }}
        </div>
        <div class="cmt-body">
          {{ item.content }}
        </div>
      </div>
    </div>
    <mt-button type="danger" size="large" plain @click="getMore()"></mt-button>
  </div>
</template>

<script>
import { Toast } from 'mint-ui'
export default {
  data() {
    return {
      pageIndex: 1, // 
      comments: [],
      msg: ''
    }
  },
  created() {
    this.getComments()
  },
  methods: {
    getComments() { // 
      this.$http.get('api/getcomments/' + this.id + '?pageindex=' + this.pageIndex)
      .then(result => {
        if(result.body.status === 0) {
          // console.log(result.body.message)
          // 
          this.comments = this.comments.concat(result.body.message)
        }else{
          Toast('')
        }
      })
    },
    getMore() { // 
      this.pageIndex++
      this.getComments()
    },
    postComment() { // 
      // 
      if(this.msg.trim().length === 0) {
        Toast('')
        return false
      }
      // 1URL
      // 2 { content: this.msg }
      // 3 { emulateJSON: true } 
      this.$http
      .post('api/postcomment/' + this.$route.params.id, { content: this.msg.trim() })
      .then(result => {
        if(result.body.status === 0){
          // 1.
          let cmt = {
            user_name: '',
            add_time: Date.now(),
            content: this.msg.trim()
          }
          // 2. unshift  comment 
          this.comments.unshift(cmt)
          this.msg = ''
        }else{
          Toast('')
        }
      })
    }
  },
  props: ['id']
}
</script>

<style lang="scss" scoped>
.cmt-container {
  h3 {
    font-size: 18px;
    font-weight: 700;
  }
  hr {
    margin: 6px 0;
    background-color: #999;
  }
  textarea {
    font-size: 14px;
    height: 85px;
    margin: 0;
  }
  .cmt-list {
    margin: 8px 0;
    .cmt-item{
      font-size: 14px;
      .cmt-title {
        line-height: 35px;
        background-color: #ccc;
      }
      .cmt-body {
        line-height: 35px;
        text-indent: 2em;
      }
    }
  }

}

</style>
